.work {
  &__column {
    &.hide-project {
      //opacity: 1!important
      animation-name: hideProject;
      animation-duration: .5s;
      animation-fill-mode: forwards; } }
  &__btn {
    background-color: #7EB5F9;
    display: inline-flex;
    align-items: center;
    padding: 10px 15px;
    letter-spacing: 0.16em;
    font-size: 14px;
    text-transform: uppercase;
    color: #ffffff;
    line-height: unset;
    cursor: pointer;
    margin: 0 0 35px 0;
    svg {
      margin: 0 15px 0 0; }
    @include breakpoint(md) {
      display: none; } }
  &__title {
    font-size: 26px;
    line-height: 30px;
    color: #2C2C2C;
    font-weight: 700;
    margin: 0 0 20px 0; }
  &__desc {
    font-size: 18px;
    line-height: 30px;
    margin: 0 0 25px 0; }
  &__sort {
    margin: 0 0 30px 0;
    ul {
      display: flex;
      justify-content: center;
      @include breakpoint(lg) {
        justify-content: flex-end; }
      li {
        &:last-child {
          a {
            margin: 0; } }
        a {
          font-size: 14px;
          line-height: 16px;
          color: #2C2C2C;
          margin: 0 20px 0 0;
          position: relative;
          &.active {
            color: #0066BA;
            &:after {
              content: '';
              position: absolute;
              width: 100%;
              height: 1px;
              background-color: #0066BA;
              bottom: -7px;
              left: 0; } } } } } } }


@keyframes hideProject {
  to {
    opacity: 0;
    min-height: 0;
    min-width: 0;
    padding: 0;
    margin: 0;
    border: 0;
    width: 0;
    height: 0; } }

.gallery-work {
  margin: 0 0 -30px 0;
  &__item {
    height: 190px;
    max-height: 190px;
    width: 100%;
    margin: 0 0 30px 0;
    position: relative;
    z-index: 1;
    overflow: hidden;
    background: rgba(0, 71, 255, 1);

    @include breakpoint(md) {
      height: 215px;
      max-height: 215px; }
    @include breakpoint(lg) {
      height: 180px;
      max-height: 180px; }
    a {
      width: 100%;
      height: 100%;
      @include breakpoint(lg) {
        &:hover {
          img {
            opacity: 0.6; }
          div {
            &:before,
            &:after {
              opacity: 1;
              transform: scale(1); }
            svg {
              transform: scale(1); } } } }
      div {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        &:before,
        &:after {
          position: absolute;
          top: 20px;
          right: 20px;
          bottom: 20px;
          left: 20px;
          content: "";
          opacity: 0;
          transition: opacity 0.5s, transform 0.5s;
          pointer-events: none; }
        &:before {
          border-top: 2px solid #fff;
          border-bottom: 2px solid #fff;
          transform: scale(0, 1); }
        &:after {
          border-right: 2px solid #fff;
          border-left: 2px solid #fff;
          transform: scale(1, 0); }
        svg {
          position: absolute;
          top: calc(50% - 24px);
          left: calc(50% - 24px);
          width: 48px;
          height: 48px;
          transition: transform 0.3s ease 0s;
          transform: scale(0);
          transform-origin: center center; } }

      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        opacity: 1;
        transition: opacity 0.3s;
        position: relative;
        display: block; } } } }



